import React from "react"
import { useStaticQuery, Link, graphql } from "gatsby"

const ListLink = props => (
    <li style={{ display: `inline-block`, marginRight: `3rem` }}>
      <Link to={props.to}>{props.children}</Link>
    </li>
  )

export default ({ children }) => {

  return (
  <div style={{ margin: `1.5rem auto`, marginBottom:'3rem', maxWidth: 768, padding: `0 1rem` }}>
       <header style={{ marginBottom: `3rem` }}>

            <ListLink to="/t3f19/">☻</ListLink>
            <ListLink to="/t3f19/exercises/">Exercises</ListLink>
            <ListLink to="/t3f19/projects/">Projects</ListLink>
            <ListLink to="/t3f19/resources/">Resourses</ListLink>

            <ul style={{ listStyle: `none`, float: `right` }}>
            <a href="https://docs.google.com/document/d/1gdcbsEzO-naDFt4ssfanukFAFqd0nafoTLdF2kuaPUk/edit?usp=sharing" target="_blank">Syllabus ↗</a>
             </ul>
        </header>
      {children}
    </div>
  )
}
